    width: 5rem;
    background-color: rgba(255, 255, 255, .2);
    border-radius: .4rem;
    border: none;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    color: black;
}

#project-menu-list-form {
