.new-task-form-inner-divs {
    display: flex;
    gap: .3rem;
    align-items: center;
    justify-content: flex-start;
}

#new-task-form-due-div {
    width: 80%;
}

#new-task-form-priority-div {
    display: flex;
    flex-direction: column;
}

#new-task-form-priority-radio-div {
    display: flex;
    font-size: 1rem;
    width: 100%;
    justify-content: space-around;
}

.radio-div {
    display: flex;
    flex-direction: column;
    align-items: center;
}
/* ***************** */

.container {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
    align-items: center;
    gap: .3rem;
    width: 100%;
    cursor: pointer;
    font-size: 1rem;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.container input {
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.checkmark {
    height: 1.7rem;
    width: 1.7rem;
    background-color: rgba(255, 255, 255, .3);
    border-radius: 50%;
}

.container:hover .checkmark {
    background-color: var(--priority-med-color);
    opacity: 1;
}


.container input:checked ~ .checkmark {
    background-color: rgba(25, 24, 26, .5);
    display: flex;
    align-items: center;
    justify-content: center;
}

.checkmark::after {
    content: "";
    display: none;
}

.container input:checked ~ .checkmark::after {
    display: block;
}

.container .checkmark::after {
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    background: rgba(25, 24, 26, .8);
}

#new-task-form-buttons-div, #new-project-form-buttons-div {
    width: 100%;
    display: flex;
    justify-content: center;
    gap: 1rem;
}

#new-task-form button {
    font-size: 1.2rem;
    height: 3rem;
    width: 5rem;
    background-color: rgba(255, 255, 255, .2);
    border-radius: .4rem;
    border: none;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    color: black;
}

#new-task-form button:hover, #new-task-form-project-input, #new-project-form button:hover {
    cursor: pointer;
    background-color: rgba(255, 255, 255, .5);
}


/* ******************** */

#task-view-div {
    background-color: var(--secondary-color);
    height: 100%;
    color: var(--main-bg-color);
    padding: 2%;
}

#task-view-title-div {
    text-align: center;
    font-size: 2rem;
}

.task {
    display: flex;
    align-items: center;
    gap: 3%;
    font-size: 1.2rem;
    height: 2rem;
}

.task:hover {
    background-color: rgba(255, 255, 255, .3);
}

.created-task-inner-div {
    display: flex;
    align-items: center;
    gap: 1rem;
    width: 100%;
    height: 2rem;
}

.created-task-due-div {
    font-size: 1rem;
    ;
}

.created-task-details-div {
    display: none;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    gap: 1rem;
    background-color: rgba(255, 255, 255, .3);
    padding: 1rem;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    font-weight: 600;
    font-size: .9rem;
}

.created-task-details-div p {
    font-weight: 400;
    font-size: .8rem;
}

.created-task-description-div {
    grid-area: 1 / 1 / 2 / 4;
}

.created-task-description-div p {
    background-color: rgba(255, 255, 255, .3);
    padding: .2rem;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
}

.created-task-project-div {
    grid-area: 2 / 1 / 3 / 2;
}

.created-task-project-div p {
    background-color: rgba(255, 255, 255, .3);
    padding: .2rem;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
}

.created-task-priority-div {
    grid-area: 3 / 1 / 4 / 2;
}

.created-task-priority-div p, .remove-task-button {
    background-color: rgba(255, 255, 255, .3);
    padding: .2rem;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
}

.remove-task-button {
    grid-area: 4 / 2 / 5 / 3;
    font-weight: 600;
}

.created-task-notes-div {
    grid-area: 2 / 2 / 4 / 4;
}

.created-task-notes-div p {
    background-color: rgba(255, 255, 255, .3);
    padding: .2rem;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    height: 80%;
}

input[type="checkbox"] {
    appearance: none;
    background-color: #fff;
    margin: 0;
    display: grid;
    place-content: center;
    font: inherit;
    color: currentColor;
    height: 1.2rem;
    width: 1.2rem;
    border-radius: .3rem;
}

input[type="checkbox"]::before {
    content: "";
    width: 1em;
    height: 1em;
    transform: scale(0);
    transition: 120ms transform ease-in-out;
    box-shadow: inset 1em 1em var(--priority-low-color);
    transform-origin: bottom left;
    clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%);
}

input[type="checkbox"]:checked::before {
    transform: scale(1);
}

#menu-div button:hover {
    background-color: rgba(255, 255, 255, .5);
