  opacity: .9;\n    display: grid;\n    grid-template-columns: 1fr 1fr 1fr;\n    font-size: 1.3rem;\n    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.25), 0 2px 8px rgba(0, 0, 0, 0.25), inset 0px 0px 0px 1px rgba(255, 255, 255, 0.1);\n    padding: 2% 0;\n}\n\n#project-menu-div, #view-by-date-div {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    text-align: center;\n    padding: 0 .5rem;\n    justify-content: space-between;\n}\n\n#create-new-buttons-div {\n    display: flex;\n    flex-direction: column;\n    justify-content: space-around;\n    gap: .7rem;\n    align-items: center;\n    height: 100%;\n}\n\n#menu-div button, #new-project-form button {\n    font-size: 1.2rem;\n    width: 5rem;\n    background-color: rgba(255, 255, 255, .2);\n    border-radius: .4rem;\n    border: none;\n    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;\n    color: black;\n}\n\n#project-menu-list-form {\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n    height: 100%;\n}\n\n#project-menu-list-select, #view-by-date-select {\n    background-color: rgba(255, 255, 255, .2);\n    border: none;\n    border-radius: .4rem;\n    font-size: 1.1rem;\n    width: 100%;\n    height: 2rem;\n}\n\nselect option {\n    background-color: var(--priority-low-color);\n}\n\n#new-project-form-div {\n    display: none;\n    background-color: white;\n    position: absolute;\n    margin: 5% 1% 0 10%;\n    z-index: 1;\n    height: 20%;\n    width: 80%;\n    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;\n}\n\n#new-task-form-div {\n    display: none;\n    background-color: white;\n    position: absolute;\n    margin: 5% 1% 0 1%;\n    z-index: 1;\n}\n\n#new-task-form, #new-project-form {\n    background-color: rgba(71, 151, 97, .8);\n    border: .2rem solid rgba(25, 24, 26, .7);\n    color: var(--main-bg-color);\n    padding: 1rem;\n    display: flex;\n    flex-direction: column;\n    gap: 1rem;\n    font-size: 1.3rem;\n}\n \n /* Input field that looks like a button */\n #new-task-form input, #new-task-form textarea, #new-project-form input {\n   width: 70%;\n   display: inline-block;\n   color: var(--priority-med-color);\n   text-align: center;\n   background-color: var(--priority-low-color);\n   padding: .5rem 1.8em;\n   border: none;\n   cursor: pointer;\n   outline: none;\n   -webkit-border-radius: 4px;\n   -moz-border-radius: 4px;\n   border-radius: 4px;\n   -webkit-transition: all .1s linear;\n   -moz-transition: all .1s linear;\n   transition: all .1s linear;\n   font-size: 1rem;\n }\n #new-task-form input:hover, #new-task-form textarea:hover, #new-project-form input:hover {\n   background-color: var(--priority-low-color);\n   opacity: .6;\n }\n\n #new-task-form select {\n    background-color: rgba(71, 151, 97, .8);\n    border: none;\n    border-radius: .4rem;\n    font-size: 1.1rem;\n}\n\n.new-task-form-inner-divs {\n    display: flex;\n    gap: .3rem;\n    align-items: center;\n    justify-content: flex-start;\n}\n\n#new-task-form-due-div {\n    width: 80%;\n}\n\n#new-task-form-priority-div {\n    display: flex;\n    flex-direction: column;\n}\n\n#new-task-form-priority-radio-div {\n    display: flex;\n    font-size: 1rem;\n    width: 100%;\n    justify-content: space-around;\n}\n\n.radio-div {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n}\n/* ***************** */\n\n.container {\n    display: flex;\n    flex-direction: column;\n    height: 100%;\n    justify-content: space-between;\n    align-items: center;\n    gap: .3rem;\n    width: 100%;\n    cursor: pointer;\n    font-size: 1rem;\n    -webkit-user-select: none;\n    -moz-user-select: none;\n    -ms-user-select: none;\n    user-select: none;\n}\n\n.container input {\n    opacity: 0;\n    cursor: pointer;\n    height: 0;\n    width: 0;\n}\n\n.checkmark {\n    height: 1.7rem;\n    width: 1.7rem;\n    background-color: rgba(255, 255, 255, .3);\n    border-radius: 50%;\n}\n\n.container:hover .checkmark {\n    background-color: var(--priority-med-color);\n    opacity: 1;\n}\n\n\n.container input:checked ~ .checkmark {\n    background-color: rgba(25, 24, 26, .5);\n    display: flex;\n    align-items: center;\n    justify-content: center;\n}\n\n.checkmark::after {\n    content: \"\";\n    display: none;\n}\n\n.container input:checked ~ .checkmark::after {\n    display: block;\n}\n\n.container .checkmark::after {\n    width: 1rem;\n    height: 1rem;\n    border-radius: 50%;\n    background: rgba(25, 24, 26, .8);\n}\n\n#new-task-form-buttons-div, #new-project-form-buttons-div {\n    width: 100%;\n    display: flex;\n    justify-content: center;\n    gap: 1rem;\n}\n\n#new-task-form button {\n    font-size: 1.2rem;\n    height: 3rem;\n    width: 5rem;\n    background-color: rgba(255, 255, 255, .2);\n    border-radius: .4rem;\n    border: none;\n    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;\n    color: black;\n}\n\n#new-task-form button:hover, #new-task-form-project-input, #new-project-form button:hover {\n    cursor: pointer;\n    background-color: rgba(255, 255, 255, .5);\n}\n\n\n/* ******************** */\n\n#task-view-div {\n    background-color: var(--secondary-color);\n    height: 100%;\n    color: var(--main-bg-color);\n    padding: 2%;\n}\n\n#task-view-title-div {\n    text-align: center;\n    font-size: 2rem;\n}\n\n.task {\n    display: flex;\n    align-items: center;\n    gap: 3%;\n    font-size: 1.2rem;\n    height: 2rem;\n}\n\n.task:hover {\n    background-color: rgba(255, 255, 255, .3);\n}\n\n.created-task-inner-div {\n    display: flex;\n    align-items: center;\n    gap: 1rem;\n    width: 100%;\n    height: 2rem;\n}\n\n.created-task-due-div {\n    font-size: 1rem;\n    ;\n}\n\n.created-task-details-div {\n    display: none;\n    grid-template-columns: 1fr 1fr 1fr;\n    grid-template-rows: 1fr 1fr 1fr 1fr;\n    gap: 1rem;\n    background-color: rgba(255, 255, 255, .3);\n    padding: 1rem;\n    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;\n    font-weight: 600;\n    font-size: .9rem;\n}\n\n.created-task-details-div p {\n    font-weight: 400;\n    font-size: .8rem;\n}\n\n.created-task-description-div {\n    grid-area: 1 / 1 / 2 / 4;\n}\n\n.created-task-description-div p {\n    background-color: rgba(255, 255, 255, .3);\n    padding: .2rem;\n    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;\n}\n\n.created-task-project-div {\n    grid-area: 2 / 1 / 3 / 2;\n}\n\n.created-task-project-div p {\n    background-color: rgba(255, 255, 255, .3);\n    padding: .2rem;\n    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;\n}\n\n.created-task-priority-div {\n    grid-area: 3 / 1 / 4 / 2;\n}\n\n.created-task-priority-div p, .remove-task-button {\n    background-color: rgba(255, 255, 255, .3);\n    padding: .2rem;\n    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;\n}\n\n.remove-task-button {\n    grid-area: 4 / 2 / 5 / 3;\n    font-weight: 600;\n}\n\n.created-task-notes-div {\n    grid-area: 2 / 2 / 4 / 4;\n}\n\n.created-task-notes-div p {\n    background-color: rgba(255, 255, 255, .3);\n    padding: .2rem;\n    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;\n    height: 80%;\n}\n\ninput[type=\"checkbox\"] {\n    appearance: none;\n    background-color: #fff;\n    margin: 0;\n    display: grid;\n    place-content: center;\n    font: inherit;\n    color: currentColor;\n    height: 1.2rem;\n    width: 1.2rem;\n    border-radius: .3rem;\n}\n\ninput[type=\"checkbox\"]::before {\n    content: \"\";\n    width: 1em;\n    height: 1em;\n    transform: scale(0);\n    transition: 120ms transform ease-in-out;\n    box-shadow: inset 1em 1em var(--priority-low-color);\n    transform-origin: bottom left;\n    clip-path: polygon(14% 44%, 0 65%, 50% 100%, 100% 16%, 80% 0%, 43% 62%);\n}\n\ninput[type=\"checkbox\"]:checked::before {\n    transform: scale(1);\n}\n\n#menu-div button:hover {\n    background-color: rgba(255, 255, 255, .5);\n}"],"sourceRoot":""}]);
